* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: rgb(166, 175, 243);
}



.signup {
    max-width: 90%;
    width: 400px;
    margin: 5rem auto;
    
    padding: 1rem;
    border-radius: 1rem;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(5px);
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: auto;
    
}

.signup h1 {
    color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;

}

#inputForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

#email, #pass, #RePass {
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: 18rem;
    max-width: 300px;
    font-size: 16px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    border: none;
    border-radius: 1rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding-left: 1rem;
    height: 3rem;
    outline: none;
}

.line-container {
    text-align: center;
    padding-left: 2rem;
    position: relative;
    margin-top: 5px;
    width: 100%;
}

.line {
    border: none;
    border-top: 2px solid #000000;
    width: 100%;
    max-width: 300px;

}

.text {
    font-size: 1rem;
    margin-top: 5px;
    color: #ffffff;
    margin-bottom: 2rem;
    text-align: center;
    justify-content: center;
}

a {
    font-weight: bold;
    color: #ffffff;
}

.SignUpButton, #SignUpBtn {
    height: 50px;
    width: 100%;
    text-align: center;
    max-width: 300px;
    color: rgb(255, 255, 255);
    font-size: 15px;
    background-color: rgba(44, 44, 44, 1);
    border: none;
    border-radius: 10px;
    cursor: pointer;
}
 
@media screen and (max-width: 600px) {
    .signup {
        width: 95%;
        padding: 1rem;
        margin: 50% auto;
    }


    #email, #pass, #RePass{
        width: 18rem;
    }
    .line, #SignUpBtn {
        width: 95%;
        
    }

    .signup h1 {
        font-size: 1.5rem;
    }
}